import React from 'react'
import { useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'
import * as selectors from '../../data/selectors'
import { withSuspense } from '@namcbugdb/react-common'

import { SamplesDataGrid } from '../SamplesDataGrid'

interface BoxDetailProps {
  boxId: number
  customerMode: boolean
}

export const BoxSamples = withSuspense(
  ({ boxId, customerMode }: BoxDetailProps): JSX.Element => {
    const boxSamples = useRecoilValue(selectors.selectBoxSamplesByBoxId(boxId))
    return (
      <SamplesDataGrid
        samples={boxSamples}
        sitesListingSource={{ type: 'box', id: boxId }}
        customerMode={customerMode}
      />
    )
  },
  () => <MuiSkeleton height="3em" variant="rectangular" />
)
